.container {
    width: 75%;
    margin: 0 auto;
}

.newDiscussion {
    width: 70%;
    margin: 35px auto;
}

.forumMenus {
    text-align: center;
    margin-bottom: 15px;
    padding: 25px 25px;
}

.discussions {
    margin-top: 25px;
    padding: 25px 25px;
    background: rgba(255, 255, 255, .7);
}


.questionTitle {
    font-size: 20px;
    font-weight: bold;
    color: rgba(0,0,0,0.6);
    margin-bottom: 10px;
}
.questionBody {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 15px;

    max-height: 100px;
    overflow: hidden;
}
.questionFooter {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.discussion {
    cursor: pointer;
    border-radius: 10px;
    padding: 25px 25px;
    margin-bottom: 25px;
    border: 1px solid #ECECEC;
}
.discussion:hover {
    background: rgba(255,255,255);
}
.discussion:last-child {
    margin-bottom: 0;
}

.postedTime {
    margin-right: 5px;
}
.postedBy {
    font-weight: bold;
    color: rgba(56,91,160);
}
.minsAgo {
    font-weight: bolder;
}


@media screen and (min-width: 1000px) {
    .newDiscussion {
        width: 20%;
        margin: 35px auto;
    } 
    .forumContainer {
        display: flex;
        justify-content: space-between;
    }
    .forumMenus {
        width: 25%;
        text-align: left;

        padding-left: 0;
    }
    .discussions {
        width: 73%;
    }
}